.app__navbar{
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1rem 2rem;
    background: rgba($color: #B2E0FF, $alpha: 0.25);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border: 1px solid rgba($color: #B2E0FF, $alpha: 0.18);
    position: fixed;
    z-index: 2;
}

.app__navbar-logo {
    font-size: 30px;
    color: black;
    font-family: 'Montserrat', sans-serif;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.app__navbar-links{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    list-style: none;

    li {
        margin: 0 1rem;
        cursor: pointer;
        flex-direction: column;
    
        a {
            color: var(--gray--color);
            text-decoration: none;
            flex-direction: column;
            font-size: medium;
            font-weight: 500;
    
            transition: all 0.3s ease-in-out;
    
            &:hover{
                color: var(--secondary-color);
            }
        }
        }

    @media screen and (max-width: 900px){
        display: none;
    }
}

.app__navbar_menu {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--secondary--color);

    svg{
        width: 90%;
        height: 90%;
        color: var(--white-color);
        background-color: gray;
        border-radius: 8px;
        

    }

    div{
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;

        padding: 1rem;

        width: 80%;

        height: 100vh;

        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;


        background-color: var(--white-color);
        background-size: cover;
        background-repeat: repeat;

        box-shadow: 0 0 20px rgba($color: gray, $alpha: 0.15);

        svg{
            width: 35px;
            height: 35px;
            color: var(--secondary-color);
            margin: 0.5rem 1rem;
            border: none;
            background-color: white;
        }

        ul{
            list-style: none;
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;

            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;

            li{
                margin: 1rem;
                
                a{
                    color: gray;
                    &:hover{
                        color: var(--secondary--color);
                    }
                }
            }
        }

        @media screen and (min-width: 900px){
            display: none;            
        }
    }
    @media screen and (min-width: 900px){
        display: none; 
    }
}